.nav {
  display: flex;
  gap: 30px;
}

.nav__link {
  font-size: 16px;
  display: block;
  color: black;
  font-weight: 700;
  line-height: 1.14;
  letter-spacing: 0.02em;
  text-decoration: none;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    text-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.nav__link:hover,
.nav__link:focus {
  color: orange;
}

.active {
  color: orange;
}